import { Fragment, useState, useEffect } from "react";
import "./UpdateProfile.css";
import Loader from "../Loading/Loading";
import MailOutlineIcon from "@material-ui/icons/MailOutline";
import FaceIcon from "@material-ui/icons/Face";
import { useDispatch, useSelector } from "react-redux";
import { clearErrors, updateProfile, loaduseraction } from "../../Actions/useraction";
import { useAlert } from "react-alert";
import { UPDATE_PROFILE_RESET } from "../../Constants/userconstants";
import MetaData from "../Layout/Metadata";
const Profile = 'https://ik.imagekit.io/shera/Images/tr:w-200,f-auto/Profile.png'

const UpdateProfile = ({ history }) => {
  const dispatch = useDispatch();
  const alert = useAlert();

  const { user } = useSelector((state) => state.user);
  const { error, isUpdated, loading } = useSelector((state) => state.profile);

  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [avatar, setAvatar] = useState('');
  const [avatarPreview, setAvatarPreview] = useState(Profile);

  
  const updateProfileSubmit = (e) => {
    e.preventDefault();
    
    const myForm = {
      Name: name,
      Email:email,
    }
    myForm.Avatar = avatar
    // console.log(myForm)
    dispatch(updateProfile(myForm));
  };
  
  const updateProfileDataChange = (e) => {
    const reader = new FileReader();
    
    reader.onload = () => {
      if (reader.readyState === 2) {
        setAvatarPreview(reader.result);
        setAvatar(reader.result)
      }
    };
    
    reader.readAsDataURL(e.target.files[0]);
  };

  let src
  if (user && user.Avatar && user.Avatar.url) {
    src = user.Avatar.url
    src = src.substring(0, 55) + "f_auto/" + src.substring(55, src.length);
  }

  useEffect(() => {
    if (user) {
      setName(user.Name);
      setEmail(user.Email);
      setAvatarPreview(src);
    }

    if (error) {
      alert.error(error);
      dispatch(clearErrors());
    }

    if (isUpdated) {
      alert.success("Profile Updated Successfully");
      dispatch(loaduseraction());

      history.push("/account");

      dispatch({
        type: UPDATE_PROFILE_RESET,
      });
    }
  }, [dispatch, error, alert, history, user, isUpdated , src]);
  return (
    <Fragment>
      {loading ? (
        <Loader />
      ) : (
        <Fragment>
          <MetaData title="Update Profile" />
          <div className="updateProfileContainer">
            <div className="updateProfileBox">
              <h2 className="updateProfileHeading">Update Profile</h2>

              <form
                className="updateProfileForm"
                encType="multipart/form-data"
                onSubmit={updateProfileSubmit}
              >
                <div className="updateProfileName">
                  <FaceIcon />
                  <input
                    type="text"
                    placeholder="Name"
                    required
                    name="name"
                    value={name}
                    onChange={(e) => setName(e.target.value)}
                  />
                </div>
                <div className="updateProfileEmail">
                  <MailOutlineIcon />
                  <input
                    type="email"
                    placeholder="Email"
                    required
                    name="email"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                  />
                </div>

                <div id="updateProfileImage">
                  <img src={avatarPreview} alt="Avatar Preview"/>
                  <input
                    type="file"
                    name="avatar"
                    accept="image/*"
                    onChange={updateProfileDataChange}
                  />
                </div>
                <input
                  type="submit"
                  value="Update"
                  className="updateProfileBtn"
                />
              </form>
            </div>
          </div>
        </Fragment>
      )}
    </Fragment>
  );
};

export default UpdateProfile;
